<template>
  <div class="detail_box">
    <div class="detail_nav">
      <div class="detail_nav_btn" @click="goBack"><i class="el-icon-back"></i></div>
      <div class="detail_nav_title">{{$t('Red Envelope Detail')}}</div>
      <div class="detail_nav_text">
        <router-link to="/"><i class="el-icon-s-home"></i></router-link>
      </div>
    </div>
    <red-top :red-obj="redObj"></red-top>
    <div class="detail_content">
      <div class="detail_content_item" style="text-align: center;">
        <qrcode :value="codeOther" :options="{ size: 230 }"></qrcode>
      </div>
      <div class="detail_content_item">
        <div class="detail_content_item_top">
          <div>{{$t('Red Envelope Id')}}</div>
          <div class="blue" @click="doCopyId">{{$t('COPY')}}</div>
        </div>
        <div class="detail_content_item_middle">{{$t('The ID allows you to view red envelope information')}}</div>
        <div class="detail_content_item_foot">{{id}}</div>
      </div>
      <div class="detail_content_item">
        <div class="detail_content_item_top">
          <div>{{$t('Red Envelope Code')}}</div>
          <div class="blue" @click="doCopy">{{$t('COPY')}}</div>
        </div>
        <div class="detail_content_item_middle">{{$t('Share the string to your friends, let your friends get the red envelope')}}</div>
        <div class="detail_content_item_foot textarea">{{code}}</div>
      </div>
    </div>
    <div class="line"></div>
    <red-data :red-obj="redObj"></red-data>

    <loading :loading-status="loading"></loading>
  </div>

</template>

<script>
import Enu from "enujs";
export default {
  components: {
    loading: resolve => {
      require(["@/components/loading.vue"], resolve);
    },
    redTop: resolve => {
      require(["@/components/redTop.vue"], resolve);
    },
    redData: resolve => {
      require(["@/components/redData.vue"], resolve);
    }
  },
  data() {
    return {
      wxUrl: globalConfig.wxUrl,
      loading: false,
      id: "",
      f: "",
      l: "",
      redObj: null,
      code: null,
      codeOther: null
    };
  },
  async created() {
    console.log(this.$route);
    this.id = this.$route.query.id;
    this.f = this.$route.query.f;
    this.l = this.$route.query.l;
    try {
      this.redObj = await this.chainApi.getRecord(this.id);
      this.code =
        this.$t("YAS Mainnet Red Envelope") +
        "\n - - - - - - - - - - - - -\n" +
        this.redObj.words +
        "\n- - - - - - - - - - - - -\n" +
        this.$t("RedInfo1") +
        this.wxUrl +
        "/#/receive?id=" +
        this.id +
        "&f=" +
        this.f +
        "&l=" +
        this.l +
        "&lang=" +
        localStorage.getItem("Lang") +
        "\n" +
        this.$t("RedInfo2") +
        "\n" +
        this.$t("RedInfo3");
      this.codeOther =
        this.wxUrl +
        "/#/receive?id=" +
        this.id +
        "&f=" +
        this.f +
        "&l=" +
        this.l +
        "&lang=" +
        localStorage.getItem("Lang");
    } catch (err) {
      this.$message.error(this.$t("RedNoExitMessage"));
    }
  },

  methods: {
    goBack() {
      window.history.go(-1);
    },
    doCopy() {
      let _this = this;
      _this.$copyText(_this.code).then(
        function(e) {
          _this.$message.success(_this.$t("Copy Success"));
        },
        function(e) {
          _this.$message.error(_this.$t("Copy Fail"));
        }
      );
    },
    doCopyId() {
      let _this = this;
      _this.$copyText(_this.id).then(
        function(e) {
          _this.$message.success(_this.$t("Copy Success"));
        },
        function(e) {
          _this.$message.error(_this.$t("Copy Fail"));
        }
      );
    }
  },
  computed: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@rem: round(1/75 * 2, 6) * 1rem;
.detail_box {
  width: 100%;
  height: 100%;
  .detail_nav {
    width: 100%;
    height: 44 * @rem;
    line-height: 44 * @rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15 * @rem;
    background: #ce2344;
    box-sizing: border-box;
    .detail_nav_btn {
      width: 30 * @rem;
      height: 44 * @rem;
      display: flex;
      align-items: center;
      i {
        color: #fff;
        font-size: 25 * @rem;
      }
    }
    .detail_nav_title {
      font-size: 12 * @rem;
      line-height: 44 * @rem;
      color: #fff;
    }
    .detail_nav_text {
      a {
        width: 30 * @rem;
        height: 44 * @rem;
        display: flex;
        align-items: center;
        text-align: right;
        i {
          color: #fff;
          font-size: 23 * @rem;
        }
      }
    }
  }
  .detail_content {
    .detail_content_item {
      padding: 0 16 * @rem;
      padding-bottom: 16 * @rem;
      box-sizing: border-box;
      .detail_content_item_top {
        margin-top: 26 * @rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 12 * @rem;
        color: #5d4220;
        height: 20 * @rem;
      }
      .detail_content_item_middle {
        color: #c9c2b7;
        margin: 2 * @rem 0 8 * @rem;
        font-size: 12 * @rem;
      }
      .detail_content_item_foot {
        width: calc(100% - 20 * @rem);
        border: 0 none;
        padding: 10 * @rem;
        margin-top: 4 * @rem;
        height: 26 * @rem;
        line-height: 26 * @rem;
        background-color: #f8f8f8;
        word-break: break-all;
        resize: none;
      }
      .textarea {
        height: 158 * @rem;
        line-height: normal;
        background-color: #fffcdd;
        width: calc(100% - 20 * @rem);
        border: 0 none;
        padding: 10 * @rem;
        margin-top: 4 * @rem;
        word-break: break-all;
        resize: none;
        white-space: pre-wrap;
      }
    }
  }
  .line {
    height: 8 * @rem;
    width: 100%;
    background-color: #f8f8f8;
  }
  .blue {
    color: #288efb !important;
    font-size: 14 * @rem;
  }
}
</style>



